:root {

    /**
     * colors
     */
  
    --rich-black-fogra-29_50: hsl(210, 26%, 11%, 0.5);
    --rich-black-fogra-29-1: hsl(210, 26%, 11%);
    --rich-black-fogra-29-2: hsl(210, 50%, 4%);
    --silver-metallic: hsl(212, 9%, 67%);
    --bg-light: #e1e1e1;
    --text-light : var(--first-color-dark);
    --coquelicot_20: hsla(295, 17%, 62%, 0.4);
    --coquelicot_10: hsla(295, 17%, 62%, 0.25);
    --sonic-silver: hsl(0, 0%, 47%);
    --cadet-gray: hsl(214, 15%, 62%);
    --light-gray: hsl(0, 0%, 80%);
    --coquelicot: hsl(294.74deg 16.89% 62.04%);
    --gainsboro: hsl(0, 0%, 88%);
    --white_20: hsl(0, 0%, 100%, 0.2);
    --white_10: hsl(0, 0%, 100%, 0.1);
    --black_10: hsl(0, 0%, 0%, 0.1);
    --white: hsl(0, 0%, 100%);
  
    /**
     * typography
     */
  
    --ff-catamaran: 'Catamaran', sans-serif;
    --ff-rubik: 'Rubik', sans-serif;
  
    --fs-1: 2.8rem;
    --fs-2: 2rem;
    --fs-3: 1.5rem;
    --fs-4: 1rem;
    --fs-5: .8rem;
    --fs-6: .5rem;
  
    --fw-900: 700;
    --fw-800: 800;
    --fw-700: 700;
    --fw-500: 500;
  
    /**
     * spacing
     */
  
    --section-padding: 80px;
  
    /**
     * shadow
     */
  
    --shadow-1: 0 0 20px var(--black_10);
    --shadow-2: 0px 10px 24px var(--coquelicot_20);
  
    /**
     * border radius
     */
  
    --radius-10: 10px;
    --radius-8: 8px;
    --radius-5: 5px;
  
    /**
     * transition
     */
  
    --transition-1: 0.25s ease;
    --transition-2: 0.5s ease;
    --cubic-in: cubic-bezier(0.51, 0.03, 0.64, 0.28);
    --cubic-out: cubic-bezier(0.33, 0.85, 0.4, 0.96);
  
  }
  
  
  
  
  *:disabled{
    opacity: 0.5;
    pointer-events: none;
    cursor:no-drop;
  }
  *,
  *::before,
  *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
    font-family: "Roboto", serif;
  }
  
  li { list-style: none; }
  
  a {
    text-decoration: none;
    color: inherit;
  }
  
  a,
  img,
  span,
  input,
  button,
  strong,
  ion-icon { display: block; }
  
  img { height: auto; }
  
  input,
  button {
    background: none;
    border: none;
    font: inherit;
  }
  
  input { width: 100%; }
  
  button { cursor: pointer; }
  
  ion-icon { pointer-events: none; }
  
  address { font-style: normal; }
  
  html {
    /* font-family: var(--ff-rubik); */
    scroll-behavior: smooth;
    font-style: normal;
  }
  
  body {
    line-height: 1.6;
    overflow-x: hidden;
  
  }
  body.light{
    background: var(--bg-light);
    color: var(--sonic-silver);
  
  }
  body.dark{
    background-color: var(--rich-black-fogra-29-1);
    color: var(--cadet-gray);
  
  }
  
  
  :focus-visible { outline-offset: 4px; }
  
  ::-webkit-scrollbar { width: 5px; }
  
  ::-webkit-scrollbar-track { background-color: var(--light-gray); }
  
  ::-webkit-scrollbar-thumb { background-color: var(--coquelicot); }
  
  ::-webkit-scrollbar-thumb:hover { background-color: var(--rich-black-fogra-29-1); }
  
  
  
  
  
  /*-----------------------------------*\
    #REUSED STYLE
  \*-----------------------------------*/
  
  .container { padding-inline: 15px; }
  
  .section { 
    padding-block: var(--section-padding);
  }
  
  .bg-dark {
    background-color: var(--rich-black-fogra-29-1);
    /* color: var(--silver-metallic); */
  }
  
  .has-bg-image {
    background-repeat: no-repeat;
    background-position: top left;
  }
  
  .has-before,
  .has-after {
    position: relative;
    z-index: 1;
  }
  
  .has-before::before,
  .has-after::after {
    content: "";
    position: absolute;
  }
  
  .h1,
  .h2,
  .h3 {
    line-height: 1.25;
  }
  
  .h1 {
  
    font-weight: var(--fw-900);
  }
  
  .h2
   {
    font-weight: var(--fw-800);
    font-size: var(--fs-2);
  }
  
  
  
  .btn {
    max-width: max-content;
    text-transform: uppercase;
    font-weight: var(--fw-500);
    padding: 15px 25px;
    border-radius: var(--radius-8);
    transition: var(--transition-1);
  }
  
  .btn-primary {
    background-color: var(--coquelicot);
    color: var(--white);
  }
  
  .btn-primary:is(:hover, :focus) {
    background-color: var(--white);
    color: var(--coquelicot);
    box-shadow: var(--shadow-2);
  }
  
  .btn-secondary {
    background-color: var(--white);
    color: var(--coquelicot);
  }
  
  .btn-secondary:is(:hover, :focus) { background-color: var(--rich-black-fogra-29-1); }
  
  .w-100 { width: 100%; }
  
  .circle,
  .abs-img { position: absolute; }
  
  .circle {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -56%);
    width: 100%;
    z-index: -1;
    animation: rotate360 15s linear infinite;
  }
  
  .circle-2 { animation-direction: reverse; }
  
  .btn-link {
    --color: var(--white);
  
    color: var(--color);
    font-weight: var(--fw-500);
    text-transform: uppercase;
    max-width: max-content;
    transition: var(--transition-1);
  }
  
  .btn-link::before {
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--color);
    transition: var(--transition-1);
  }
  
  .btn-link:is(:hover, :focus) { --color: var(--coquelicot); }
  
  .text-center { text-align: center; }
  
  .img-holder {
    aspect-ratio: var(--width) / var(--height);
    background-color: var(--light-gray);
    overflow: hidden;
    height: 200px;
  }
  
  .img-cover {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .has-scrollbar {
    display: flex;
    gap: 25px;
    overflow-x: auto;
    padding-block-end: 30px;
    scroll-snap-type: inline mandatory;
  }
  
  .scrollbar-item {
    min-width: 100%;
    scroll-snap-align: start;
  }
  
  .has-scrollbar::-webkit-scrollbar { height: 10px; }
  
  .has-scrollbar::-webkit-scrollbar-track,
  .has-scrollbar::-webkit-scrollbar-thumb { border-radius: 50px; }
  
  .has-scrollbar::-webkit-scrollbar-thumb:hover { background-color: var(--coquelicot); }
  
  .has-scrollbar::-webkit-scrollbar-button { width: calc(25% - 25px); }
  
  
  
  
  .back-top-btn {
    position: fixed;
    bottom: 20px;
    right: 40px;
    background-color: var(--coquelicot);
    color: var(--rich-black-fogra-29-1);
    padding: 11px;
    border-radius: 50%;
    border: 2px solid var(--rich-black-fogra-29-1);
    visibility: hidden;
    opacity: 0;
    transition: var(--transition-1);
    z-index: 4;
  }
  
  .back-top-btn.active {
    visibility: visible;
    opacity: 1;
    transform: translateY(-10px);
  }
  
  
  .swiper-button-next , .swiper-button-prev{
    position: absolute;
    width: 50px !important;
    height: 50px !important;
    transform: translateY(-50%);
    top: 50%;
  }
  
  /* .swiper-button-next{
      right: -5% !important;
  }
  .swiper-button-prev{
    left: -5% !important;
  } */
  .swiper-button-prev::after , .swiper-button-next::after{
    outline: none;
    border: none;
    width: 100% !important;
    height: 100% !important;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--coquelicot) !important ;
    background: var(--black_10) !important;
    border-radius: 50% !important
  }
  .swiper-button-prev::after:hover , .swiper-button-next:hover::after{
    background-color: var(--coquelicot) !important;
    color: var(--black_10) !important;
  }
  .top{
    margin: 4rem auto 0 auto;
  
  }
  